@font-face {
  font-family: "Signature";
  src: url("/font/signature/signature.ttf");
  src: url("/font/signature/signature.ttf") format("embedded-opentype"),
    url("/font/signature/signature.ttf") format("woff"),
    url("/font/signature/signature.ttf") format("truetype"),
    url("/font/signature/signature.ttf") format("svg");
  font-style: normal;
  font-weight: normal;
}

